<template>
    <div class="mobile_obituary_user active"><!-- 장례식장 사이드메뉴-->
		<div class="mobile_obituary_user_inner">
			<h1>주소록 사용자 선택</h1>
				<h2 style="float: right"> 전체선택 <input type="checkbox" class="rightspace" v-model="allChecked" @change="allClick"><span class="rightspace"></span></h2>
			<div class="address" style="margin-top:10px;">

				
			<table>
			<colgroup>
				<col width="85%">
				<col width="15%">
			</colgroup>
			<tbody>
			<tr style="border-bottom:1px solid #cccccc;">
				<td><input   type="text" class="input01" placeholder="이름을 입력하세요"  v-model="name"></td>
			</tr>
			<tr style="border-bottom:1px solid #cccccc;">
				<td><input  type="text" class="input01" placeholder="전화번호를 입력하세요"  @keypress="onlyNumber" v-model="phone"></td>
			</tr>
			</tbody>
			</table> 
			
			<div style="width:100%; height:280px; overflow:auto;overflow-x:hidden;">
			<table cellspacing="0" cellpadding="0" border="0">
			<tr v-for="(item,index) in items" :key="index" style="color:#999; border-bottom:1px solid #ccc;"  @click="registAddress(address)">
				<td width="90%" style="padding:8px 0 8px;"><p>{{item.display_name}}</p> {{phoneNumber(item.phone)}}</td>
				<td style="text-align:right;">
				<input type="checkbox" class="rightspace" v-model="item.checked"><span class="rightspace"></span></td>
			</tr>
			</table>  
			</div>
		</div>
		</div>
		
		<div class="btn_area"><button class="btn_confirm"  @click="transSms()">문자전송</button></div>
		<button class="btn_address_colse" @click="transClose()">닫기</button>
	</div>
</template>

<script>
import { mapGetters } from 'vuex'
import myMixin from '@/mixin/mixin'
export default {
	name:'MobileContactsList',
	props:['pitems','transClose','methods'],
  mixins: [myMixin],
	data(){
		return{
			address:'',
			allChecked:true,
			name:'',
			phone:'',
			items:[]
		}
	},
	mounted(){
		this.items = this.pitems
	},
	computed:{
		...mapGetters(['']),
	},
	methods:{
		allClick(event){
			let itemsCopy = JSON.parse(JSON.stringify(this.items))
			for(let i=0;i<itemsCopy.length;i++){
				itemsCopy[i].checked = this.allChecked
			}
			this.items = itemsCopy
			
		},
		phoneNumber(number){
			let contents = ''
			for(let i=0;i<number.length;i++){
				if(contents === ''){
					contents += number[i].number
				}else{
					
					contents += ',' + number[i].number
				}
			}
			return contents
		},
		transSms(){
			if(this.name==='' || this.phone===''){
				alert("구매자의 성항과 핸드폰번호를 입력해 주세요.");
				return;
			}
			let items = []
			for(let i=0;i<this.items.length;i++){
				if(this.items[i].checked){
					const item = {
						name: this.items[i].display_name,
						phone: this.phoneNumber(this.items[i].phone)
					}
					items.push(item)
				}
			}
			const parameter = {
				items:items,
				name: this.name,
				phone: this.phone
			}

			this.methods(parameter)

		},
	}
}
</script>