<template>
    <div class="mobile_obituary_user active"><!-- 장례식장 사이드메뉴-->
		<div class="mobile_obituary_user_inner">
			<h1>상주선택</h1>
				<h2 style="float: right"> 전체선택 <input type="checkbox" class="rightspace" v-model="allChecked" @change="allClick"><span class="rightspace"></span></h2>
			<div class="address" style="margin-top:10px;">
			<div style="width:100%; height:350px; overflow:auto;overflow-x:hidden">
			<table cellspacing="0" cellpadding="0" border="0">
			<tr v-for="(item,index) in items" :key="index" style="color:#999; border-bottom:1px solid #ccc;"  @click="registAddress(address)">
				<td width="90%" style="padding:8px 0 8px;"><p>{{item.obituaryMemberType}}</p>   {{item.obituaryMemberName}}</td>
				<td style="text-align:right;">
				<input type="checkbox" class="rightspace" v-model="item.checked"><span class="rightspace"></span></td>
			</tr>
			</table>  
			</div>
		</div>
		</div>
		
			<div class="btn_area"><button class="btn_confirm"  @click="contactsSelect()">주소록 사용자 선택</button></div>
		<button class="btn_address_colse" @click="transClose()">닫기</button>
	</div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
	name:'AddressSearch',
	props:['methods','pitems','transClose'],
	data(){
		return{
			address:'',
			allChecked:false,
			items:[]
		}
	},
	mounted(){
		this.items = this.pitems
	},
	computed:{
		...mapGetters(['getAddressSearchList']),
	},
	methods:{
		allClick(event){
			let itemsCopy = JSON.parse(JSON.stringify(this.items))
			for(let i=0;i<itemsCopy.length;i++){
				itemsCopy[i].checked = this.allChecked
			}
			this.items = itemsCopy
			
		},
		searchAddress(){
			this.$store.dispatch('REQUEST_ADDRESS_SEARCH_LIST',this.address+'장례식장')
		},
		contactsSelect(address){
			console.log('### complete.html complete 함수 수행')
			let item = []
			for(let i=0;i<this.items.length;i++){
				if(this.items[i].checked){
					item.push(this.items[i])
				}
			}
			this.methods(item)
		    window.nsWebViewBridge.emit("transContacts");
			//this.transClose()
		},
	}
}
</script>