<template>
	<div style="margin:0;padding:0;height: 80vh;" id="apps">
		<!-- wrap -->
		<div id="wrap" :key="getObituaryList"  style="min-height:100%;position:relative">
			<!-- header -->
				<div id="deemed2" v-if="mobileUserPopupFlag || mobileContactsPopupFlag || mobileSmsPopupFlag"></div>
                <MobileUserList v-if="mobileUserPopupFlag" :methods="selectRealObituaryUserList" :pitems="obituaryUserItems" :transClose="transClose" style="top:15%;position: fixed;"/>
                <MobileContactsList v-if="mobileContactsPopupFlag" :methods="selectRealTransSms" :pitems="obituaryContactsItems" :transClose="transClose"  style="top:15%;position: fixed;"/>
                <MobileSmsTxt v-if="mobileSmsPopupFlag" :methods="smsPushTrans" :items="smsTransTxtVlue" :transClose="transClose"  style="top:15%;position: fixed;"/>
			<header id="sub">
				<div class="header_wrap">
					<h1>부고검색</h1>
				</div>
			</header>
			<!-- header -->
			<!-- search area -->
			<div id="search_area">
				<select class="selectStyle" v-model="searchType">
						<option value="name">고인명</option>
						<option value="master">상주명</option>
						<option value="phone">상주전화번호</option>
					</select>
				<div class="searchinput">
					<input type="text" placeholder="검색어를 입력해주세요" class="inputStyle" v-model="searchTxt" @keyup.enter="requestSearch()">
					<button class="searchBtnStyle" @click="requestSearch()">검색버튼</button>
				</div>
			</div>
			<!-- //search area -->
			<div class="bugo_tab_area">
				<ul class="bugo_tab">
					<li @click="requestSearch('ing')" :class="endFlag==='ing'?'active':''">장례식중</li>
					<li @click="requestSearch('end')" :class="endFlag==='end'?'active':''">발인</li>
					<li @click="requestSearch('all')" :class="endFlag==='all'?'active':''">전체</li>
				</ul>
			</div>
			<!-- content -->
			<div :class="['bugo_tab_contents bugo-first',bugoAddCladd]"  >
				<!-- 전체 -->
				<div v-for="(obituary,index) in getObituaryList" :key="index" class="bugo_list">
					<p class="style01">
						<span v-if="obituary.obituaryMember.memberAttr.codeId === 'TYPE-0'">{{obituary.obituaryMortuary!==null?obituary.obituaryMember.memberName + ' ' + obituary.obituaryMortuary.mortuaryNumber:obituary.obituaryMember.memberName}}</span>
						<span v-if="obituary.obituaryMember.memberAttr.codeId === 'TYPE-1'">{{obituary.obituaryAddrName + ' ' + obituary.obituaryMortuaryName}}</span>
						<span v-if="obituary.endFlag==='Y'">장례중</span>
						<span v-if="obituary.endFlag==='N'" class="vacancy">발인</span>
					</p>
					<p class="style02">
						<span>고인명 : {{obituary.obituaryName}}</span>
						
						<template v-if="obituary.obituaryObituaryMember===null || obituary.obituaryObituaryMember===undefined || obituary.obituaryObituaryMember.length===0">
							<span > : 상주없음 </span>

						</template>
						<template v-else v-for="(obituaryMember,index) in obituary.obituaryObituaryMember">
							<span v-if="obituaryMember.obituaryMemberLeader" :key="index"> : {{obituaryMember.obituaryMemberName}} 외 {{obituary.obituaryObituaryMember.length}}명 </span>
           				 </template>
					</p>
					<p class="style03">
						<span>발인일시 : <span class="bugo_number">{{YYSLASHDate(obituary.obituaryEndDate)}}</span></span>
						<span>등록일 : <span class="bugo_number">{{YYSLASHDate(obituary.regDate)}}</span></span>
					</p>
					<p class="style04">
						<span @click="transAllMember(obituary)">문자전송</span>
					</p>	
				</div>
			</div>
			<div id="contents">
				<div class="paging">
					<button v-if="startPage!==1" @click="onPageChange(startPage-1)" class="prev">이전</button>
					<a :class="page===getObituaryPage?'on':''" v-for="(page,index) in pages()" :key="index" @click="onPageChange(page)">{{page}}</a>
					<button v-if="getObituaryPageTotal>endPage"  @click="onPageChange(endPage+1)" class="next">다음</button>
				</div>
			</div>


		</div>
		
							<!-- footer -->
				<div  v-if="!mobileUserPopupFlag && !mobileContactsPopupFlag && !mobileSmsPopupFlag" id="footer_m" style="width:100%;height: 20vh;;bottom:0;padding:0 0 0 0">
					<div class="footer_inner">
						<dl>
							<div style="display:inline">
							<h3 @click="webPopupMobile('https://jrbaksa.com/secure2')" style="display: inline;font-size:15px;margin-right: 30px;">이용약관</h3><br/>
							<h2 @click="webPopupMobile('https://jrbaksa.com/secure')" style="display: inline;font-size:15px;">개인정보 처리규정</h2><br/>
							<h2 @click="webPopupMobile('https://jrbaksa.com')" style="display: inline;font-size:15px;">장례박사로 이동</h2><br/>
							</div>
							<dt>장례박사</dt> 
							<dd>사업자등록번호 : 491-02-01801 </dd>
							<dd>통신판매업신고 : 제2020-서울노원-2088호</dd>
							<dd>경기도 남양주시 두물로27번길28  202호</dd>
							<dd>대표명 : 유성훈 김천명</dd>
							<dd>대표 전화번호 : <span class="footer_n">1688-2734</span></dd>
							<!--<dd>이메일주소 : <span class="footer_n">janglaebacsa@naver.com</span></dd>-->
						</dl>
						<!--<p class="usebtn"><span><a href="#">개인정보처리방침</a></span><span><a href="#">이용약관</a></span></p>-->
						<p class="copyright">@2020 JANGLAEBACSA ALL RIGHTS RESERVED</p>
						<div class="btm_logo"></div>
						<div class="logo"/>
							<!-- <h1>24시간 고객센터</h1>
							<a href="tel:1688-2734"><span>1599-0000</span></a> -->
						</div>
					</div>
				</div>
				<!-- //footer -->

	</div>
</template>
<script>
import Vue from 'vue'
import Footer from '@/components/common/Footer'

import myMixin from '@/mixin/mixin'

import { mapGetters } from 'vuex'
import  MobileUserList from '@/components/mobile/MobileUserList'
import  MobileContactsList from '@/components/mobile/MobileContactsList'
import  MobileSmsTxt from '@/components/mobile/MobileSmsTxt'


import { eventBus } from '@/main'

export default {
	name: 'ObituaryListForMobile',
	mixins: [myMixin],
	components: {
		Footer,
		MobileUserList,
		MobileContactsList,
		MobileSmsTxt
	},
	data() {
		return {
			mobileUserPopupFlag: false,
			mobileContactsPopupFlag: false,
			obituary: {
				id: null,
				obituaryName: null,
				obituaryAge: null,
				obituaryAgeYn: null,
				obituarySex: null,
				obituarySexYn: null,
				obituaryMortuary: null,
				obituaryCode: null,
				obituaryDieYn: null,
				obituaryDieDate: null,
				obituaryEnterDate: null,
				obituaryCasketDate: null,
				obituaryCasketYn: null,
				obituaryReligionDate: null,
				obituaryReligionYn: null,
				obituaryEndDate: null,
				obituaryPlace: null,
				obituaryText: null,
				page: {
					page: this.$route.query.page ? this.$route.query.page : 0,
					size: this.$route.query.size ? this.$route.query.size : 8,
					sort: this.$route.query.sort ? this.$route.query.sort : 'regDate,desc',
				}
			},
			obituaryUserItems:[],
			obituaryContactsItems:[],
			realObituaryUserList:[],
			realObituaryContactsList:[],
			realObituaryId:null,
			smsTransTxtVlue:{
				
				smsTransTxt: '',
			},
			mobileSmsPopupFlag : false,
			endFlag: this.$route.query.endFlag ? this.$route.query.endFlag : 'ing',
			searchTxt: this.$route.query.searchTxt ? this.$route.query.searchTxt : '',
			searchType: this.$route.query.searchType ? this.$route.query.searchType : 'name',
			pages: function() {
				const list = [];
				for (let i = this.startPage; i <= this.endPage; i += 1) list.push(i);
				return list
			},
		}
	},
	computed: {
		...mapGetters(['getObituaryList', 'getObituaryPage', 'getObituaryPageTotal', 'getObituaryTotalElements', "getObituaryCount"]),
		startPage() {
			return parseInt((this.getObituaryPage - 1) / 5) * 5 + 1
		},
		endPage() {
			return (parseInt((this.getObituaryPage - 1) / 5) + 1) * 5 < this.getObituaryPageTotal ? (parseInt((this.getObituaryPage - 1) / 5) + 1) * 5 : this.getObituaryPageTotal
		},
		bugoAddCladd() {
			if (this.endFlag === 'all') {
				return 'bugo-first'
			} else if (this.endFlag === 'ing') {
				return 'bugo-first'
			} else {
				return ''
			}
		},
	},
	created() {
			
	},
	beforeMount() {
	},
	mounted() {
	
		const context =this
		eventBus.$on('mobileBridge', (val) => {
			if(val.type === 'transContactsList'){
				context.transContactsList(val.value)
			}
		});
		if(this.searchTxt.length>1){

			this.requestGetObituaryList()
		}
	},
	beforeDestroy() {
    	eventBus.$off('mobileBridge')
		this.$store.commit('OBITUARY_INIT')
	},
	methods: { 
		transContactsList(args){
			let context = this					
			context.obituaryContactsItems = args
			for(let i=0;i<context.obituaryContactsItems.length;i++){
				context.obituaryContactsItems[i].checked = true
			}
			context.mobileUserPopupFlag= false
			context.mobileContactsPopupFlag= true

		},
		/*
		makeBridege(e){
			alert('add')
     		 e.preventDefault()
			let context = this
			var nsWebViewBridge = e.detail || e.nsWebViewBridge;
			
				nsWebViewBridge.on('transContactsList', function (args) {
					context.obituaryContactsItems = args
					for(let i=0;i<context.obituaryContactsItems.length;i++){
						context.obituaryContactsItems[i].checked = true
					}
					context.mobileUserPopupFlag= false
					context.mobileContactsPopupFlag= true
				});
		},
		*/
		obituaryReg() {
			this.$router.push('/obituaryReg')
		},
		obituaryUpdate(id) {
			this.$router.push({
				path: '/obituaryUpdate',
				query: {
					id: id
				}
			})
		},
		requestGetObituaryList() {
			const parameter = {
				obituary: this.obituary,
				endFlag: this.endFlag,
				searchTxt: this.searchTxt,
				searchType: this.searchType,
			}
			this.$store.dispatch("REQUEST_OBITUARY_FOR_MOBILE_LIST", parameter)
		},
		requestSearch(flag) {
			if(this.searchTxt.length<=1){
				alert("검색어 2글자 이상을 입력하세요.");
				return
			}
			if(flag!==null && flag!==undefined){
				this.endFlag = flag
			}
			this.requestObituaryList()
		},
		onPageChange(val) {
			const param = {
				page: val
			}
			this.requestObituaryList(param)
		},
		requestObituaryList(param) {
			let currentPage = 0;
			if (param !== undefined && param !== null &&
				param.page !== undefined && param.page !== null) {
				currentPage = param.page - 1;
			}
			this.$router.push({
				path: '/obituaryForMobile',
				query: {
					endFlag: this.endFlag,
					searchTxt: this.searchTxt,
					searchType: this.searchType,
					page: currentPage,
					size: this.obituary.page.size,
					sort: this.obituary.page.sort
				}
			})
		},
		transAllMember(obituary){
			
			this.obituaryUserItems = obituary.obituaryObituaryMember
			for(let i=0;i<this.obituaryUserItems.length;i++){
				this.obituaryUserItems[i].checked = false
			}
			
			this.realObituaryId = obituary.id
			this.realObituaryUserList= []
			this.realObituaryContactsList= []
			this.mobileUserPopupFlag = true
		},
		selectRealObituaryUserList(val){
			this.realObituaryUserList= val
		},
		transClose(){
			this.obituaryUserItems = []
			this.obituaryContactsItems = []
			this.realObituaryUserList= []
			this.realObituaryContactsList= []
			this.realObituaryId = null
			this.mobileUserPopupFlag = false
			this.mobileContactsPopupFlag = false
			this.mobileSmsPopupFlag = false
			this.smsTransTxtVlue = {}
		},
		selectRealTransSms(val){
			const parameter ={
				value:{
					contactsList: val.items,
					obituaryUserList:this.realObituaryUserList,
					obituaryId:this.realObituaryId,
					name:val.name,
					phone:val.phone
				}
			}
			const context = this

			this.$store.dispatch("REQUEST_OBITUARY_TRANS_SMS", parameter).then(data => {
				
				if (data.result) {
					context.smsTransTxtVlue = {
						smsTransTxt : data.value.obituary.obituaryName+ "님께서 "+data.value.obituary.obituaryCode.codeName+"하셨기에 아래와 같이 부고를 전해 드립니다.\n\n■부고■\n" + '' + "\n\n황망한 마음에 일일이 연락 드리지 못함을 널리 혜량해주시길 바랍니다."
						,smsCount : data.value.phoneCount,
						smsPrice : data.value.phonePrice,
						url:data.url
					}
					context.mobileSmsPopupFlag = true

				}
			})

			this.transClose()
		},
		smsPushTrans(val){
			$(location).attr('href', val.url+'&SmsTxt='+encodeURIComponent(val.smsTransTxt));
		},
		webPopupMobile(path){
			
			const parameter = {
				url:path
			}
			
		    window.nsWebViewBridge.emit("newPopupWeb", { url: path });

		}
	}
}
</script>
